import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    authenticated: false,
  },
  plugins: [createPersistedState()],
  mutations: {
    setAuthentication(state, status) {
      state.authenticated = status
    },
  },
  actions: {
  },
  modules: {
  }
})
